import { Injectable } from '@angular/core';
import { EdResourceService } from './ed-resource.service';
import { ValueFormatterParams } from 'ag-grid-community/dist/lib/entities/colDef';
import { DatePipe, DecimalPipe } from '@angular/common';
import { StudyListService, AppDataService } from 'so-common-library';
import { ICON_CLEAR_TEMPLATE, GEOGRAPHY_LEVEL, MAPPED_COHORT, ICON_DONE_TEMPLATE } from '@app4/app.constant';

@Injectable({
  providedIn: 'root'
})
export class EdColDefService {
  columnResources: any;
  private reportingFreqData: any;

  constructor(private resourceService: EdResourceService,
    public studyListService: StudyListService,
    public appDataService: AppDataService) {

  }

  getAutogroupColDef() {
    return {
      headerName: this.resourceService.columnResource.SITE_ID,
      rowGroup: true,
      cellRendererParams: {
        suppressCount: true
      },
      cellStyle: (params) => {
        if (params.data.isLeafNode) {
          return { 'padding-left': '30px', 'text-align': 'left' };
        } else {
          return null;
          // return { 'text-align': 'left' };
        }
      },
      cellDoubleClicked: (params) => {
        this.redirectToScoreCardsTab();
      },
      pinned: true,
      width: 200
    };
  }

  setReportingFreq(reportingFreqData) {
    this.reportingFreqData = reportingFreqData;
  }

  getColumnDef() {
    const columnResources = this.resourceService.columnResource;
    const colDefs = [];
    // #cohort
    colDefs.push(this.getColDefWithResourceData({
      field: 'cohortName',
    }, columnResources.COHORT));

    // #siteName
    colDefs.push(this.getColDefWithResourceData({
      field: 'name',
    }, columnResources.SITE_NAME));

    // #regionName
    colDefs.push(this.getColDefWithResourceData({
      field: 'regionName',
      rowGroup: true,
    }, columnResources.REGION));
    // #countryName
    colDefs.push(this.getColDefWithResourceData({
      field: 'countryName',
      rowGroup: true,
    }, columnResources.COUNTRY));

    // #investigatorName
    colDefs.push(this.getColDefWithResourceData({
      field: 'investigatorName',
    }, columnResources.INVESTIGATOR_NAME));

    // #monitor
    colDefs.push(this.getColDefWithResourceData({
      field: 'monitors',
      // valueGetter: this.monitorListValueGetter
    }, columnResources.MONITOR));

    // #sourceFSI
    colDefs.push(this.getColDefWithResourceData({
      field: 'srcFSIDt',
      valueFormatter: this.dateFormatter
    }, columnResources.SOURCE_FSI));

    // #siteSelectionVisit
    colDefs.push(this.getColDefWithResourceData({
      field: 'actualSSVDt',
      valueGetter : this.siteSlectionVisitValueGetter,
      valueFormatter: this.dateFormatter
    }, columnResources.SITE_SELECTION_VISIT));

    // #initiationDate

    colDefs.push(this.getColDefWithResourceData({
      field: 'siteInitiatedDt',
      valueFormatter: this.dateFormatter
    }, columnResources.INITIATION_DATE));

    // #cohortReadinessDate
    colDefs.push(this.getColDefWithResourceData({
      field: 'crd',
      valueFormatter: this.dateFormatter
    }, columnResources.COHORT_READINESS_DATE));

    // #cohortActivityDate
    colDefs.push(this.getColDefWithResourceData({
      field: 'cohortActivityExpected',
      cellRenderer: (params) => {
        if (params.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
          if (params.data.isCohActExp === true) {
            return ICON_DONE_TEMPLATE;
          } else {
            return ICON_CLEAR_TEMPLATE;
          }
        } else {
          return params.value;
        }
      },
      tooltip: (params) => {
        if (params.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
          if (params.data.isCohActExp) {
            return columnResources.COHORT_ACTIVITY_EXPECTED_TOOLTIP;
          } else {
            return columnResources.COHORT_ACTIVITY_NOT_EXPECTED_TOOLTIP;
          }
        }
        return params.value;
      }
      // valueGetter: this.cohortActivityExpectedValueGetter
    }, columnResources.COHORT_ACTIVITY_DATE));

    // #cohortPlanMapping
    colDefs.push(this.getColDefWithResourceData({
      field: 'cohortMapping',
      cellRenderer: (params) => {
        if (params.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
          if (params.data.isMappedInPlan === true && params.data.cohortName) {
            return ICON_DONE_TEMPLATE;
          } else if (params.data.cohortName) {
            return ICON_CLEAR_TEMPLATE;
          } else {
            return null;
          }
        } else {
          return params.value;
        }
      },
      tooltip: (params) => {
        if (params.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
          if (params.data.isMappedInPlan === true && params.data.cohortName) {
            return columnResources.SITE_MAPPED_COHORT_TOOLTIP;
          } else if (params.data.cohortName) {
            return columnResources.SITE_UNMAPPED_COHORT_TOOLTIP;
          } else {
            return null;
          }
        }
        return params.value;
      }
    }, columnResources.COHORT_PLAN_MAPPING));

    // #FSSDate
    colDefs.push(this.getColDefWithResourceData(
      {
        field: 'actualFSFVDt',
        valueFormatter: this.dateFormatter
      }, columnResources.FIRST_SUB_SCREENED_DATE));

    colDefs.push(this.getColDefWithResourceData(
      {
        field: 'siToFSS',
      }, columnResources.FSI_TO_FSS));

    // #screeningPerf
    const screeningPerfChildren = [
      this.getColDefWithResourceData(
        {
          field: 'totalScreened'
        }, columnResources.TOTAL),
      this.getColDefWithResourceData(
        {
          field: 'subInScreening',
          cellClass: (params) => {
            if (params.value < 0) {
              return 'red-text';
            } else {
              return null;
            }
          }
        }, columnResources.IN_SCREENING),
      this.getColDefWithResourceData({
        field: 'exitScreened',
      }, columnResources.COMPLETED),
      this.getColDefWithResourceData({
        field: 'totalScreenFailed',
      }, columnResources.FAILED),
      this.getColDefWithResourceData({
        field: 'screenFailureRate',
        valueFormatter: this.oneDecimalPercFormatter,
      }, columnResources.ACTUAL_SCREEN_FAILURE_PERC),
      this.getColDefWithResourceData({
        field: 'screenRate',
        valueFormatter: (params: ValueFormatterParams) => {
          const numberPipe = new DecimalPipe('en_US');
          if (isNaN(params.value)) {
            return '';
          }
          return numberPipe.transform(
            (this.reportingFreqData ? (params.value * this.reportingFreqData.reportingFreq) : params.value), '1.0-3');
        }
      }, this.reportingFreqData ?
      columnResources.ACTUAL_SR.replace('{0}', columnResources[this.reportingFreqData.reportingFreqLabel]) : columnResources.ACTUAL_SR )
    ];

    // #randomizationPerf
    colDefs.push(this.getColDefWithResourceData(
      {
        field: '',
        children: screeningPerfChildren,
        headerClass: 'group-header-class',
        marryChildren: true
      }, columnResources.SCREENING_PERFORMANCE));

    const randomizationPerfChildren = [
      this.getColDefWithResourceData(
        {
          field: 'totalPlanEnrolled',
        }, columnResources.SOURCE_TARGET),
      this.getColDefWithResourceData(
        {
          field: 'randomized',
        }, columnResources.PLAN_TARGET),
      this.getColDefWithResourceData({
        field: 'actualEnrolled',
      }, columnResources.ACTUAL_RANDOMIZATION),
      this.getColDefWithResourceData({
        field: 'RR',
        // valueFormatter: this.threeDecimalFormatter,
        // valueGetter: (params) => {
        //   return isNaN(params.data.subjectsPerDay) ? null : reportingFreqData.reportingFreq * params.data.subjectsPerDay;
        // },
        valueFormatter: (params: ValueFormatterParams) => {
          const numberPipe = new DecimalPipe('en_US');
          if (isNaN(params.value)) {
            return '';
          }
          return numberPipe.transform(
            (this.reportingFreqData ? (params.value * this.reportingFreqData.reportingFreq) : params.value), '1.0-3');
        }
      // }, columnResources.ACTUAL_RR.replace('{0}', reportingFreqLabelValue))
      }, this.reportingFreqData ?
      columnResources.ACTUAL_RR.replace('{0}', columnResources[this.reportingFreqData.reportingFreqLabel]) : columnResources.ACTUAL_RR )
    // }, columnResources.ACTUAL_RR)
    ];
    colDefs.push(this.getColDefWithResourceData(
      {
        field: '',
        children: randomizationPerfChildren,
        headerClass: 'group-header-class',
        marryChildren: true
      }, columnResources.RANDOMIZATION_PERFORMANCE));

    // #latestSiteActivity
    const latestSiteActivityChildren = [
      this.getColDefWithResourceData(
        {
          field: 'latestSubjScrnDt',
          valueFormatter: this.dateFormatter
        }, columnResources.SCREENED),
      this.getColDefWithResourceData(
        {
          field: 'latestSubjRandDt',
          valueFormatter: this.dateFormatter
        }, columnResources.RANDOMIZED),
      this.getColDefWithResourceData({
        field: 'stopProjDt',
        valueFormatter: this.dateFormatter
      }, columnResources.STOP_PROJECTION),
      this.getColDefWithResourceData({
        field: 'ModifiedDt',
        valueFormatter: this.dateFormatter
      }, columnResources.MODIFIED_DATE),
      this.getColDefWithResourceData({
        field: 'ModifiedBy',
      }, columnResources.MODIFIED_BY),
      this.getColDefWithResourceData({
        field: 'noProjContInd',
        cellRenderer: (params) => {
            if (params.value === true) {
              return ICON_CLEAR_TEMPLATE;
            } else if (params.value === false) {
              return ICON_DONE_TEMPLATE;
            } else {
              return null;
            }
        },
        tooltip: (params) => {
          if (params.value === true) {
            return columnResources.SITE_NOT_INCLUDED_IN_PROJECTION_TOOLTIP;
          } else if (params.value === false) {
            return columnResources.SITE_INCLUDED_IN_PROJECTION_TOOLTIP;
          } else {
            return params.value;
          }
        }
      }, columnResources.PROJECTIONS)
    ];
    colDefs.push(this.getColDefWithResourceData({
      children: latestSiteActivityChildren,
      headerClass: 'group-header-class',
      marryChildren: true
    }, columnResources.LATEST_SITE_ACTIVITY));

    // #retention
    const retentionChildren = [
      this.getColDefWithResourceData(
        {
          field: 'totalDropOuts',
        }, columnResources.DROPOUTS),
      this.getColDefWithResourceData({
        field: 'dropOutRate',
        valueFormatter: this.oneDecimalPercFormatter
      }, columnResources.DROPOUTS_PERC),
    ];
    colDefs.push(this.getColDefWithResourceData(
      {
        field: '',
        children: retentionChildren,
        headerClass: 'group-header-class',
        marryChildren: true
      }, columnResources.RETENTION));

    colDefs.push(this.getColDefWithResourceData(
      {
        field: 'activeDuration',
      }, columnResources.ENROLLMENT_DURATION));
    colDefs.push(this.getColDefWithResourceData({
      field: 'actualLSSDt',
      valueFormatter: this.dateFormatter
    }, columnResources.LSS_DATE));
    colDefs.push(this.getColDefWithResourceData({
      field: 'actualLSFVDt',
      valueFormatter: this.dateFormatter
    }, columnResources.LSR_DATE));
    colDefs.push(this.getColDefWithResourceData({
      field: 'status',
    }, columnResources.SITE_STATUS));
    // colDefs.push(this.getColDefWithResourceData({
    //   field: 'isCoe',
    // }, columnResources.CENTER_OF_EXCELLENCE));
    // colDefs.push(this.getColDefWithResourceData({
    //   field: 'notesLength',
    // }, columnResources.NOTES));
    return colDefs;
  }

  getColDefWithResourceData(colDef: any, resourceValue: string) {
    return Object.assign({
      headerName: resourceValue,
      headerTooltip: resourceValue,
      colName: resourceValue,
      colValueToMap: resourceValue,
      width: 200,
      enableValue: true,
      tooltip: function (params) {
        return (params.valueFormatted ? params.valueFormatted : params.value);
      }
    }, colDef);
  }

  getDefaultColumnDef() {
    return {
      width: 100,
      editable: false,
      menuTabs: ['filterMenuTab', 'columnsMenuTab'],
      filterParams: { suppressMiniFilter: true },
      cellStyle: { 'text-align': 'center' },
      lockPinned: true
    };
  }

  public dateFormatter = (params: ValueFormatterParams) => {
    const datePipe = new DatePipe('en_US');
    let date = null;
    if (params.value instanceof Date) {
      date = params.value;
    } else {
      date = new Date((params.value || '').replace(/-/g, '/'));
    }
    if (!params.value || isNaN(date.getTime())) {
      return '';
    }

    return datePipe.transform(date, 'dd-MMM-yyyy');
  }

  public threeDecimalFormatter = (params: ValueFormatterParams) => {
    const numberPipe = new DecimalPipe('en_US');
    if (isNaN(params.value)) {
      return '';
    }

    return numberPipe.transform(params.value, '1.0-3');
  }

  public monitorListValueGetter = (params) => {
    console.log('monitor : ', params.value);
    if (params.data.centerMonitorList && params.data.centerMonitorList.centerMonitor) {
      return [].concat(params.data.centerMonitorList.centerMonitor).map(data => data.monitorName).join();
    } else {
      return '';
    }
  }

  public oneDecimalFormatter = (params: ValueFormatterParams) => {
    const numberPipe = new DecimalPipe('en_US');
    if (isNaN(params.value)) {
      return '';
    }

    return numberPipe.transform(params.value, '1.0-1');
  }

  public oneDecimalPercFormatter = (params: ValueFormatterParams) => {
    const numberPipe = new DecimalPipe('en_US');
    if (isNaN(params.value)) {
      return '';
    }

    return numberPipe.transform(params.value, '1.0-1') + '%';
  }

  getReportingPeriodResource(columnResources: any, label: string): string {
    switch (label) {
      case 'DAILY':
        return columnResources.DAILY;
      case 'WEEK':
        return columnResources.WEEKLY;
      case 'MONTH':
        return columnResources.MONTHLY;
      case 'QUARTER':
        return columnResources.QUARTERLY;
      default:
        return '';
    }
  }

  getResourceForMapping() {
    const resources = this.resourceService.columnResource;
    return {
      OF: resources.OF,
      ALL: resources.ALL
    };
  }

  redirectToScoreCardsTab() {
    console.log('Redirect to score card tab');
  }

  public siteSlectionVisitValueGetter = (params) => {
    if (params.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
      return params.data.countryActualSSVDt;
    } else {
      return params.data.actualSSVDt;
    }
  }
}
