import 'ag-grid-enterprise';
import {
  ColumnApi,
  ColumnVisibleEvent,
  GridApi,
  GridColumnsChangedEvent,
  GridReadyEvent,
  ICellRendererParams,
  RefreshCellsParams,
  RowDataUpdatedEvent,
  RowNode,
  SortChangedEvent,
  GridOptions,
  DragStoppedEvent,
  Column,
  RowDataChangedEvent,
  RowGroupOpenedEvent,
  ViewportChangedEvent
} from 'ag-grid-community';
import { Component, OnInit, ViewEncapsulation, OnDestroy, Renderer2, ViewChild, ElementRef } from '@angular/core';
import { UserDataService, StudySummaryService, GROUP_ACTIONS, DOMLAYOUT,
   PrefGridColumnsKeys, ColumnPreferenceService} from 'so-common-library';
import { EnrollmentDetailsService } from './enrollment-details.service';
import { EdColDefService } from './grid/ed-col-def.service';
import { AppUtil } from 'common-library';
import { EdGridService } from './grid/ed-grid.service';
import { GridFilterService } from '../grid-filter.service';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { GEOGRAPHY_LEVEL, MAPPED_COHORT } from '@app4/app.constant';
import { environment } from 'environments/environment';

@Component({
  selector: 'ctos-enrollment-details',
  templateUrl: './enrollment-details.component.html',
  providers: [StudySummaryService, EnrollmentDetailsService],
  encapsulation: ViewEncapsulation.None
})
export class EnrollmentDetailsComponent implements OnInit, OnDestroy {

  // AG-Grid variables
  private gridApi: GridApi;
  private columnApi: ColumnApi;
  private gridColumnApi;
  getDataPath: (data: any) => any;
  autoGroupColumnDef: any;
  columnDefs;
  context;
  defaultColDef: any;
  frameworkComponents: any;
  rowData = [];
  gridOptions: GridOptions;
  groupDefaultExpanded = -1;
  aggFuncs;

  rowClassRules = {
    'ag-row-focus': function (params) {
      return params.node.isSelected();
    }
  };
  @ViewChild('agGrid', { read: ElementRef, static: true }) edGrid: ElementRef;
  @ViewChild('gridCardWrapper', { read: ElementRef, static: true }) gridCardWrapper: ElementRef;

  private readonly subscriptions: Subject<void> = new Subject<void>();

  constructor(private userDataService: UserDataService,
    private enrollmentDetailsService: EnrollmentDetailsService,
    private edColDefService: EdColDefService,
    private edGridService: EdGridService,
    private gridFilterService: GridFilterService,
    private columnPreferencesService: ColumnPreferenceService,
    private renderer: Renderer2) { }

  ngOnInit() {
    this.autoGroupColumnDef = this.edColDefService.getAutogroupColDef();
    this.getDataPath = function (data) {
      return data.geoHierarchy;
    };
    this.setGridOptions();
    this.setDefaultColumnDef();
    this.isExternalFilterChanged();
    this.edColDefService.setReportingFreq(this.enrollmentDetailsService.getReportingFreqData(this.getStudyKey()));
    this.setColumnDef();
  }

  getStudyKey(): number {
    return this.userDataService.getLastVisitedStudyKey();
  }

  getContext() {
    return {
      componentParent: this
    };
  }

  setDefaultColumnDef() {
    this.defaultColDef = this.edColDefService.getDefaultColumnDef();
  }

  setColumnDef() {
    this.columnDefs = this.edColDefService.getColumnDef();
  }

  disableArrowKeys(params): boolean {
    const KEY_LEFT = 37;
    const KEY_UP = 38;
    const KEY_RIGHT = 39;
    const KEY_DOWN = 40;

    // return true (to suppress) if editing and user hit up/down keys
    const keyCode = params.event.keyCode;
    const gridShouldDoNothing =
      params.editing &&
      (keyCode === KEY_UP ||
        keyCode === KEY_DOWN ||
        keyCode === KEY_LEFT ||
        keyCode === KEY_RIGHT);

    return gridShouldDoNothing;
  }

  setGridOptions() {
    this.gridOptions = {
      domLayout: DOMLAYOUT.normal,
      suppressMovableColumns: false,
      suppressNoRowsOverlay: true,
      enableFilter: true,
      enableSorting: true,
      rowSelection: 'single',
      rowMultiSelectWithClick: false,
      singleClickEdit: true,
      suppressContextMenu: environment.toSuppressContextMenu,
      suppressDragLeaveHidesColumns: true,
      suppressRowClickSelection: false,
      excludeChildrenWhenTreeDataFiltering: true,
      getRowClass: (params) => {
        return '';
      },
    };
  }

  getRowNodeId(data) {
    let id = null;
    switch (data.geographyLevel) {
      case 'WW':
        id = 'WW';
        break;
      case 'Region':
        id = data.geographyKey;
        break;
      case 'Country':
        id = data.geographyKey;
        break;
      case 'Site':
        id = data.studyCenterKey;
        break;
      case 'Cohort':
        id = data.countryName + '_' + data.studyCohortKey;
        break;
      default:
        id = data.geographyKey;
        break;
    }
    return id;
  }

  gridReady(params: GridReadyEvent) {
    console.log('gridheight : grid ready');
    this.gridApi = params.api;
    this.columnApi = params.columnApi;
    this.gridColumnApi = params.columnApi;
    this.enrollmentDetailsService.getEnrollmentDetailsGridData(this.getStudyKey()).pipe(takeUntil(this.subscriptions)).subscribe(data => {
      if (data) {
        console.log('gridheight : setting row data grid ready subscription');
        this.rowData = data.rowData;
        const reportingFreqData = this.enrollmentDetailsService.getReportingFreqData(this.getStudyKey());
        this.edColDefService.setReportingFreq(reportingFreqData);
        this.edGridService.refreshColumnData(reportingFreqData, this.columnApi);
        this.gridApi.refreshHeader();
        this.edGridService.showHideCohortColumns(this.columnApi, data.isCohortStudy);
        this.gridApi.onFilterChanged();
      } else {
        console.log('Grid Data not received');
        // throw new Error('Error in loading data for grid from API');
      }
    });
    this.setGridPrefences();

  }
   setGridPrefences(): void {
    const self = this;
    if (self.
      columnPreferencesService.getPrefForGridColumns().
      PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID] !== '' &&
      self.
      columnPreferencesService.getPrefForGridColumns().
      PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID] !== undefined) {
        self.columnApi.setColumnState(self.
          columnPreferencesService.getPrefForGridColumns().PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID]);
      }
    self.columnPreferencesService.getGridPreference().subscribe(data => {
      if (data) {
     if (self.
      columnPreferencesService.getPrefForGridColumns().
      PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID] !== '' &&
      self.
      columnPreferencesService.getPrefForGridColumns().
      PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID] !== undefined) {
        self.columnApi.setColumnState(self.
          columnPreferencesService.getPrefForGridColumns().PreferenceGridColumns[PrefGridColumnsKeys.SITE_ENROLLMENT_GRID]);
        }
     }
    });
  }
  columnVisible(event: ColumnVisibleEvent) {
    const self = this;
    if (!event.column) {
      return;
    }
    self.columnPreferencesService.sendUpdateColumnPrefRequest(
      self.gridColumnApi.getColumnState(), PrefGridColumnsKeys.SITE_ENROLLMENT_GRID);
    // sessionStorage.setItem(PrefGridColumnsKeys.SITE_DATA_MANAGEMENT_GRID, this.gridColumnApi.getColumnState()) ;
  }


  dragStopped(event: DragStoppedEvent): void {
    this.columnPreferencesService.sendUpdateColumnPrefRequest(
      this.gridColumnApi.getColumnState(), PrefGridColumnsKeys.SITE_ENROLLMENT_GRID);
  }

  firstDataRendered(event) {
    console.log('gridheight : firstDataRendered');
    event.api.refreshClientSideRowModel('filter');
  }

  onRowDataUpdated(event) {
    console.log('gridheight : onRowDataUpdated');
    event.api.refreshClientSideRowModel('filter');
  }

  rowDataChanged(event: RowDataChangedEvent) {
    console.log('gridheight : rowDataChanged');
    this.setCountryGridHeight(event, GROUP_ACTIONS.EXPAND);
  }
  onrowGroupOpened(event: RowGroupOpenedEvent) {
    console.log('gridheight : onrowGroupOpened');
    this.setCountryGridHeight(event, GROUP_ACTIONS.EXPAND);
  }

  setCountryGridHeight(event, action = GROUP_ACTIONS.EXPAND) {
    const parentDiv = this.edGrid.nativeElement;
    const gridCardWrapperHeight = this.gridCardWrapper && this.gridCardWrapper.nativeElement.offsetHeight;
    const gridHeight = this.edGridService.setGridHeight(action, event.api, gridCardWrapperHeight);
    this.renderer.setStyle(parentDiv, 'height', gridHeight + 'px');
    event.api.refreshView();
  }

  groupRowAggNodes(nodes) {
    const result = {
      totalScreened: 0,
      subInScreening: 0,
      exitScreened: 0,
      totalScreenFailed: 0,
      totalPlanEnrolled: 0,
      screenFailureRate: 0,
      screenRate: 0,
      actualEnrolled: 0,
      RR: 0,
      multipliedSubjectsPerDay: 0,
      totalDropOuts: 0,
      dropOutRate: 0,
      // srcFSIDt: null,
      siteInitiatedDt: null,
      cohortMapping: null,
      cohortActivityExpected: null,
      // actualLSSDt: null,
      // actualLSFVDt: null,
      actualFSSVCountForRegion: 0,
      actualFSSVCountForWW: 0,
      totalScreenRateForRegion: 0,
      totalScreenRateForWW: 0,

      actualFSSVCountForRegionForRR: 0,
      actualFSSVCountForWWForRR: 0,
      totalRRForRegion: 0,
      totalRRForWW: 0
    };
    let cohortActivityExpectedCount = 0;
    let cohortActivityExpectedTotalCount = 0;
    let mappedCohortCount = 0;
    let totalCohortCount = 0;
    let actualFSSVCount = 0;

    let actualFSSVCountForSubjectPerPay = 0;

    let currentGeoLevel = null;
    nodes.forEach((node) => {
      const resources = (this && this.edColDefService && this.edColDefService.getResourceForMapping()) || { ALL: 'ALL', OF: 'of' };
      let data = null;
      if (node.data) {
        if (node.data.isLeafNode) {
          data = node.data;
        } else {
          data = node.aggData;
        }

        if (!data) {
          console.log('data is undefined : ', node);
        }
        if (data && data.totalScreened && typeof data.totalScreened === 'number' && !data.cohortID) {
          result.totalScreened += data.totalScreened;
        } else {
          if (data && data.siteKey) {
            result.totalScreened = data.siteTotalScreenedCount;
          }
        }

        if (data && typeof data.subInScreening === 'number' && !data.cohortID) {
          result.subInScreening += data.subInScreening;
        } else {
          if (data && data.siteKey) {
            result.subInScreening = data.siteSubInScreenedCount;
          }
        }

        if (data && typeof data.exitScreened === 'number' && !data.cohortID) {
          result.exitScreened += data.exitScreened;
        } else {
          if (data && data.siteKey) {
            result.exitScreened = data.siteExitScreenedCount;
          }
        }

        if (data && typeof data.totalScreenFailed === 'number' && !data.cohortID) {
          result.totalScreenFailed += data.totalScreenFailed;
        } else {
          if (data && data.siteKey) {
            result.totalScreenFailed = data.siteTotalScreenFailedCount;
          }
        }
        result.screenFailureRate = (result.totalScreenFailed / result.exitScreened) * 100;

        if (data && typeof data.screenRate === 'number' && !data.cohortID) {
          if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
            if (node.data.actualFSFVDt && !isNaN(node.data.screenRate) && node.data.screenRate > 0) {
              actualFSSVCount++;
              result.actualFSSVCountForRegion++;
              result.totalScreenRateForRegion += (data.screenRate);
              result.screenRate += (node.data.screenRate);
            }
            currentGeoLevel = GEOGRAPHY_LEVEL.SITE;
          } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
            result.totalScreenRateForRegion += data.totalScreenRateForRegion;
            result.actualFSSVCountForRegion += data.actualFSSVCountForRegion;
            currentGeoLevel = GEOGRAPHY_LEVEL.COUNTRY;
          } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.REGION) {
            currentGeoLevel = GEOGRAPHY_LEVEL.REGION;
            result.actualFSSVCountForWW += data.actualFSSVCountForRegion;
            result.totalScreenRateForWW += data.totalScreenRateForRegion;
          } else {
            actualFSSVCount++;
            result.screenRate += (node.data.screenRate);
          }
        } else {
          if (node.data && node.data.siteKey) {
            result.screenRate = node.data.siteScreenRate;
          }
        }

        if (data && typeof data.RR === 'number' && !data.cohortID) {
          // result.totalScreenFailed += data.totalScreenFailed;
          if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
            if (node.data.actualFSFVDt && !isNaN(node.data.RR)) {
              actualFSSVCountForSubjectPerPay++;
              result.actualFSSVCountForRegionForRR++;
              // result.actualFSSVCountForWW++;
              result.totalRRForRegion += (node.data.RR);
              // result.totalScreenRateForWW += (data.RR);
              result.RR += (node.data.RR);
            }
            currentGeoLevel = GEOGRAPHY_LEVEL.SITE;
          } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
            result.totalRRForRegion += data.totalRRForRegion;
            // result.totalScreenRateForWW += data.totalScreenRateForWW;
            result.actualFSSVCountForRegionForRR += data.actualFSSVCountForRegionForRR;
            // result.actualFSSVCountForWW += data.actualFSSVCountForWW;
            currentGeoLevel = GEOGRAPHY_LEVEL.COUNTRY;
          } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.REGION) {
            currentGeoLevel = GEOGRAPHY_LEVEL.REGION;
            result.actualFSSVCountForWWForRR += data.actualFSSVCountForRegionForRR;
            result.totalRRForWW += data.totalRRForRegion;
          } else {
            actualFSSVCountForSubjectPerPay++;
            result.RR += (node.data.RR);
            // if (data && data.siteKey) {
            //   result.screenRate = data.siteScreenRate;
            // }
          }
        } else {
          if (node.data && node.data.siteKey) {
            result.RR = node.data.siteRR;
          }
        }



        if (data && typeof data.totalPlanEnrolled === 'number' && !data.cohortID) {
          result.totalPlanEnrolled += data.totalPlanEnrolled;
        } else {
          if (data && data.siteKey) {
            result.totalPlanEnrolled = data.siteTotalPlanEnrolled;
          }
        }

        if (data && typeof data.actualEnrolled === 'number' && !data.cohortID) {
          result.actualEnrolled += data.actualEnrolled;
        } else {
          if (data && data.siteKey) {
            result.actualEnrolled = data.siteActualEnrolled;
          }
        }

        if (data && typeof data.multipliedSubjectsPerDay === 'number' && !data.cohortID) {
          result.multipliedSubjectsPerDay += data.multipliedSubjectsPerDay;
        } else {
          if (data && data.siteKey) {
            result.multipliedSubjectsPerDay = data.siteMultipliedSubjectsPerDay;
          }
        }

        if (data && typeof data.totalDropOuts === 'number' && !data.cohortID) {
          result.totalDropOuts += data.totalDropOuts;
        } else {
          if (data && data.siteKey) {
            result.totalDropOuts = data.siteTotalDropOuts;
          }
        }
        if (result.actualEnrolled === 0 && result.totalDropOuts > 0) {
          result.dropOutRate = 100;
        } else {
          const calcDropOutRate = (result.totalDropOuts / result.actualEnrolled) * 100;
          result.dropOutRate = isNaN(calcDropOutRate) ? 0 : calcDropOutRate;
        }

        // // #srcFSIDt
        // if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //   data = node.aggData;
        // } else {
        //   data = node.data;
        // }
        // if (data) {
        //   if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
        //     if (AppUtil.isDefined(result.srcFSIDt)) {
        //       if (!AppUtil.dateCompare(result.srcFSIDt, data.srcFSIDt)) {
        //         result.srcFSIDt = data.srcFSIDt;
        //       }
        //     } else {
        //       result.srcFSIDt = data.srcFSIDt;
        //     }
        //   } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //     result.srcFSIDt = null;
        //   } else {
        //     result.srcFSIDt = data.srcFSIDt;
        //   }
        // }
        // // #siteInitiatedDt
        // if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //   data = node.aggData;
        // } else {
        //   data = node.data;
        // }
        // if (data) {
        //   if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
        //     if (AppUtil.isDefined(result.siteInitiatedDt)) {
        //       if (!AppUtil.dateCompare(result.siteInitiatedDt, data.siteInitiatedDt)) {
        //         result.siteInitiatedDt = data.siteInitiatedDt;
        //       }
        //     } else {
        //       result.siteInitiatedDt = data.siteInitiatedDt;
        //     }
        //   } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //     result.siteInitiatedDt = null;
        //   } else {
        //     result.siteInitiatedDt = data.siteInitiatedDt;
        //   }
        // }
        // // #isCohActExp
        if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
          data = node.aggData;
        } else {
          data = node.data;
        }
        if (data) {
          if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
            if (data.isCohActExp) {
              cohortActivityExpectedCount++;
            }
            cohortActivityExpectedTotalCount++;
            if (nodes.length === cohortActivityExpectedTotalCount) {
              if (cohortActivityExpectedCount === cohortActivityExpectedTotalCount) {
                result.cohortActivityExpected = resources.ALL;
              } else {
                result.cohortActivityExpected = cohortActivityExpectedCount + ' ' + resources.OF + ' ' + cohortActivityExpectedTotalCount;
              }
            }
          } else {
            result.cohortActivityExpected = null;
          }
        }

        // // # cohortID or Cohort Mapping
        if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
          data = node.aggData;
        } else {
          data = node.data;
        }
        if (data) {
          if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COHORT) {
            if (data.isMappedInPlan === true) {
              mappedCohortCount++;
            }
            totalCohortCount++;
            if (nodes.length === totalCohortCount) {
              if (mappedCohortCount === totalCohortCount) {
                result.cohortMapping = resources.ALL;
              } else {
                result.cohortMapping = mappedCohortCount + ' ' + resources.OF + ' ' + totalCohortCount;
              }
            }
          } else {
            result.cohortMapping = null;
          }
        }
        // // #actualLSSDt
        // if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //   data = node.aggData;
        // } else {
        //   data = node.data;
        // }
        // if (data) {
        //   if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
        //     if (AppUtil.isDefined(result.actualLSSDt)) {
        //       if (!AppUtil.dateCompare(data.actualLSSDt, result.actualLSSDt)) {
        //         result.actualLSSDt = data.actualLSSDt;
        //       }
        //     } else {
        //       result.actualLSSDt = data.actualLSSDt;
        //     }
        //   } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //     result.actualLSSDt = null;
        //   } else {
        //     result.actualLSSDt = data.actualLSSDt;
        //   }
        // }
        // //  #actualLSFVDt
        // if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //   data = node.aggData;
        // } else {
        //   data = node.data;
        // }
        // if (data) {
        //   if (node.data.geographyLevel === GEOGRAPHY_LEVEL.SITE) {
        //     if (AppUtil.isDefined(result.actualLSFVDt)) {
        //       if (!AppUtil.dateCompare(data.actualLSFVDt, result.actualLSFVDt)) {
        //         result.actualLSFVDt = data.actualLSFVDt;
        //       }
        //     } else {
        //       result.actualLSFVDt = data.actualLSFVDt;
        //     }
        //   } else if (node.data.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        //     result.actualLSFVDt = null;
        //   } else {
        //     result.actualLSFVDt = data.actualLSFVDt;
        //   }
        // }
      }
    });

    if (currentGeoLevel === GEOGRAPHY_LEVEL.SITE) {
      result.screenRate = (result.screenRate / actualFSSVCount);
      result.RR = (result.RR / actualFSSVCountForSubjectPerPay);
    } else if (currentGeoLevel === GEOGRAPHY_LEVEL.COUNTRY) {
      result.screenRate = result.totalScreenRateForRegion / result.actualFSSVCountForRegion;
      result.RR = result.totalRRForRegion / result.actualFSSVCountForRegionForRR;
    } else if (currentGeoLevel === GEOGRAPHY_LEVEL.REGION) {
      result.screenRate = result.totalScreenRateForWW / result.actualFSSVCountForWW;
      result.RR = result.totalRRForWW / result.actualFSSVCountForWWForRR;
    } else {
    }
    return result;
  }

  isExternalFilterChanged() {
    this.enrollmentDetailsService.externalFilterChanged()
      .pipe(takeUntil(this.subscriptions))
      .subscribe(data => {
        if (data) {
          this.gridApi.onFilterChanged();
          this.gridApi.refreshCells();
        }
      });
  }

  isExternalFilterPresent() {
    return true;
  }

  doesExternalFilterPass = (node) => {
    return this.gridFilterService.filterEnrollmentGridData(node);
  }

  ngOnDestroy(): void {
    this.subscriptions.next();
    this.subscriptions.complete();
  }

}
