import { Injectable } from '@angular/core';
import { StudyListService, AppDataService } from 'so-common-library';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { SiteFilterService } from '../sites-filter/sites-filter.service';
import { SitesService } from '../sites.service';
import { GEOGRAPHY_LEVEL, SITE_API_FORKJOIN_SEQ, SELECT_WORLD_WIDE } from '@app4/app.constant';



@Injectable()
export class EnrollmentDetailsService {

  constructor(
    private sitesService: SitesService,
    private siteFilterService: SiteFilterService,
    public studyListService: StudyListService,
    public appDataService: AppDataService
  ) { }

  getEnrollmentDetailsGridData(studyKey?: number): Observable<any> {
    return this.sitesService.getDataObservable().pipe(
      // catchError(error => of(error)),
      map(response => {
        if (response) {
          console.log('response data : ', response);
          const data = this.getRowData(response[SITE_API_FORKJOIN_SEQ.CENTER_SUMMARY],
            response[SITE_API_FORKJOIN_SEQ.RECRUITMENT_CENTER_LIST],
            response[SITE_API_FORKJOIN_SEQ.STUDY_SUMMARY]);
          const studyID = this.sitesService.getLastVisitedStudyKey();
          return { rowData: data, isCohortStudy: this.sitesService.isCohortStudy(studyID) };
        } else {
          return null;
        }
      })
    );
  }

  getReportingFreqData(studyKey: number) {
    return this.sitesService.getReportingPeriod(studyKey);
  }

  getRowData(centerSummaryData: any, recruitmentCenterListData: any, studySummaryData: any) {
    let centerSummaryModel: any = [];
    if (this.isValidCenterSummaryData(centerSummaryData)) {
      centerSummaryModel = this.convertCenterSummaryModel([].concat(centerSummaryData.centerSummary.centerThumbnail));
    }
    const studyID = this.sitesService.getLastVisitedStudyKey();
    const isCohortStudy = this.sitesService.isCohortStudy(studyID);
    if (this.isValidRecruitmentCenterListData(recruitmentCenterListData)) {
      return this.convertRecruitmentCenterListModel([].concat(recruitmentCenterListData.recruitmentCenterList.recGeoList.geography),
        centerSummaryModel, isCohortStudy);
    } else {
      return [];
    }
  }

  isValidCenterSummaryData(centerSummaryData: any): boolean {
    return centerSummaryData && centerSummaryData.centerSummary && centerSummaryData.centerSummary.centerThumbnail;
  }

  isValidRecruitmentCenterListData(recruitmentCenterListData): boolean {
    return (recruitmentCenterListData
      && recruitmentCenterListData.recruitmentCenterList
      && recruitmentCenterListData.recruitmentCenterList.recGeoList
      && recruitmentCenterListData.recruitmentCenterList.recGeoList.geography);
  }

  isCohortStudy(studySummaryData: any): boolean {
    return studySummaryData
      && studySummaryData.studyThumbnail
      && studySummaryData.studyThumbnail.cohortList
      && studySummaryData.studyThumbnail.cohortList.cohortPlanParams
      && [].concat(studySummaryData.studyThumbnail.cohortList.cohortPlanParams).length > 0;
  }

  convertRecruitmentCenterListModel(geographies: any[], centerSummaryModel: any, isCohortStudy: boolean) {
    const rowData: any[] = [];
    let currentRegion = null;
    if (!geographies) {
      return;
    }
    for (let index = 0; index < geographies.length; index++) {
      const element = geographies[index];
      if (element.geographyLevel === GEOGRAPHY_LEVEL.COUNTRY) {
        element.regionKey = currentRegion.geographyKey;
        element.regionName = currentRegion.geographyName;
        element.geoHierarchy = [SELECT_WORLD_WIDE, element.regionName, element.geographyName];
        rowData.push(element);
        element.centerList.recCenterThumbnail = (element.centerList && element.centerList.recCenterThumbnail)
          ? [].concat(element.centerList.recCenterThumbnail) : [];
        const centerRowData = this.getCenterDataFromCountry(element,
          element.centerList.recCenterThumbnail, centerSummaryModel, isCohortStudy);
        rowData.push(...centerRowData);
      } else if (element.geographyLevel === GEOGRAPHY_LEVEL.REGION) {
        element.regionName = element.geographyName;
        element.geoHierarchy = [SELECT_WORLD_WIDE, element.geographyName];
        rowData.push(element);
        currentRegion = element;
      } else if (element.geographyLevel === GEOGRAPHY_LEVEL.WORLDWIDE) {
        element.geoHierarchy = [SELECT_WORLD_WIDE];
        rowData.push(element);
      }
    }
    return rowData;
  }

  convertCenterSummaryModel(centerThumbnail: any[]) {
    const model = {};
    if (!centerThumbnail) {
      return;
    }
    for (let index = 0; index < centerThumbnail.length; index++) {
      const element = centerThumbnail[index];
      model[element.studyCenterKey] = element;
    }
    return model;
  }

  getCenterDataFromCountry(country: any, centers: any[], centerSummaryModel: any, isCohortStudy: boolean) {
    const rowData = [];
    if (!centers) {
      return;
    }
    for (let index = 0; index < centers.length; index++) {
      const element = {
        ...centers[index]
      };
      element.geographyLevel = GEOGRAPHY_LEVEL.SITE;
      element.countryName = country.geographyName;
      element.countryKey = country.geographyKey;
      element.regionKey = country.regionKey;
      element.regionName = country.regionName;
      element.isLeafNode = !isCohortStudy;
      element.screenRate = element.screenRate;
      element.RR = element.subjectsPerDay;
      Object.assign(element, centerSummaryModel[element.studyCenterKey]);
      element.monitors = this.getCenterMonitors(element.centerMonitorList);
      element.geoHierarchy = [SELECT_WORLD_WIDE, element.regionName, element.countryName, element.id];
      rowData.push(element);
      if (element.cohortDetails) {
        const cohortDetailRows = this.getCohortDetailRowData(element);
        rowData.push(...cohortDetailRows);
      }
    }
    return rowData;
  }

  getCenterMonitors(centerMonitorList): string {
    if (centerMonitorList && centerMonitorList.centerMonitor) {
      return [].concat(centerMonitorList.centerMonitor).map(data => data.monitorName).join();
    } else {
      return '';
    }
  }

  getCohortDetailRowData(siteRow: any) {
    const cohortDetailRows = [];
    const cohorts = siteRow && siteRow.cohortDetails && siteRow.cohortDetails.cohortParams
      ? [].concat(siteRow.cohortDetails.cohortParams) : [];
    for (let index = 0; index < cohorts.length; index++) {
      const element = { ...cohorts[index] };
      element.siteKey = siteRow.studyCenterKey;
      element.siteTotalScreenedCount = siteRow.totalScreened;
      element.siteSubInScreenedCount = siteRow.subInScreening;
      element.siteExitScreenedCount = siteRow.exitScreened;
      element.siteTotalScreenFailedCount = siteRow.totalScreenFailed;
      element.siteScreenFailureRate = siteRow.screenFailureRate;
      element.siteScreenRate = siteRow.screenRate;
      element.screenRate = element.screenRate;
      element.siteRR = siteRow.RR;
      element.RR = element.subjectsPerDay;
      element.siteTotalPlanEnrolled = siteRow.totalPlanEnrolled;
      element.investigatorName = siteRow.investigatorName;
      element.centerMonitorList = siteRow.centerMonitorList;
      element.monitors = siteRow.monitors;
      element.name = siteRow.name;
      element.isLeafNode = !siteRow.isLeafNode;

      element.siteActualEnrolled = siteRow.actualEnrolled;
      element.siteMultipliedSubjectsPerDay = siteRow.multipliedSubjectsPerDay;

      element.siteTotalDropOuts = siteRow.totalDropOuts;
      element.siteDropOutRate = siteRow.dropOutRate;

      element.countryName = siteRow.country;
      element.regionName = siteRow.region;
      element.countryKey = siteRow.countryKey;
      element.regionKey = siteRow.regionKey;
      element.geoHierarchy = [SELECT_WORLD_WIDE, siteRow.region, siteRow.country, siteRow.id,
      (element.cohortName ? element.cohortName : element.cohortID)];
      element.geographyLevel = GEOGRAPHY_LEVEL.COHORT;
      cohortDetailRows.push(element);
    }
    return cohortDetailRows;
  }

  externalFilterChanged(): Observable<Boolean> {
    return this.siteFilterService.filterHasBeenChanged$;
  }

  getReportingPeriod(studyKey: number): number {
    const reportingPriodFreq = this.studyListService.getStudyInfoByKey(
      studyKey
    );
    let reportingFreq: any = {};
    if (reportingPriodFreq) {
      reportingFreq = this.appDataService.getReportingFreqDefByName(
        reportingPriodFreq.reportingPeriod
      );
    }
    return reportingFreq.Value;
  }
}
